
import { Tooltip } from 'bootstrap/dist/js/bootstrap.esm.min.js';
import $ from 'jquery';
import {
  berserkFury,
  chainOfVengeance,
  doubleSlash,
  assaultBlade,
  strikeWave,
  whirlwind,
  crimeHazard,
  hellBlade,
} from '@/helpers/berserker.js';
export default {
  name: 'Berserker',
  components: {},
  data: function () {
    return {};
  },
  props: {},
  computed: {
    berserkFury,
    chainOfVengeance,
    assaultBlade,
    doubleSlash,
    strikeWave,
    whirlwind,
    crimeHazard,
    hellBlade,
  },
  methods: {
    readyTooltip() {
      this.$nextTick(() => {
        $('[data-bs-toggle="tooltip"]').tooltip();
      });
    },
  },
  mounted() {
    //inti tooltip
    Array.from(document.querySelectorAll('[data-bs-toggle="tooltip"]')).forEach(
      (tooltipNode) => new Tooltip(tooltipNode)
    );
  },
};
